import { Subscription } from 'rxjs';


@Component({
  templateUrl: './ad.component.html',
  styleUrls: ['./ad.component.scss']
})
export class AdComponent implements OnDestroy {
  private subs = new Subscription();

  constructor(
  ) {
  }

  ngOnDestroy(): void {
    this.subs.unsubscribe();
  }
}
